:root {
  --darkpink: #ab4493;
  --lightpink: #f045c8;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #ab4493;
  background-color: var(--darkpink);
}

@media screen and (max-width: 950px) {
  :root {
    background-color: white;
    padding: 0px;
  }

  #root {
    background-color: white;
    padding: 0px;
  }

  .estiloGenero {
    width: 100%;
    height: 100%;
    font-size: 15px;
    border-radius: 0;
    padding: 20px;
    background-color: #fbfbfb;
  }

  .estiloFormularioPersona {
    width: 100%;
    height: 100%;
    font-size: 15px;
    border-radius: 0;
    padding: 20px;
    background-color: #fbfbfb;
  }

  .estiloPersona {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-color: #ab4493;
    border-style: solid;
    border-radius: 10px;
    padding: 30px;
    width: 80%;
  }

  .estiloLibro {
    /* width: 100%; */
    height: 100%;
    font-size: 12px;
    border-radius: 0;
    padding: 20px;
    background-color: #fbfbfb;
  }

  .search-libro {
    margin-left: 60px;
  }
  .search-genero {
    width: 25%;
  }

  .libro-card td {
    width: 5vw;
    text-align: center;
    font-size: 10px !important;
  }

  #modal-libros-contenido table tbody {
    display: flex;
    flex-direction: column;
    font-size: 12px;
  }

  #modal-libros-contenido table tbody tr {
    display: flex;
    flex-direction: row;
    margin-left: -5%;
    justify-content: space-around;
  }

  #modal-libros-contenido table tbody tr th {
    margin-left: 10%;
  }

  #modal-libros-contenido td {
    margin-left: 0;
    display: flex;
    align-items: center;
  }

  #modal-libros-contenido {
    width: 60%;
    padding: 0% 1% 0% 1%;
    border-radius: 2% !important;
  }

  #modal-libros-close button {
    font-size: 25px;
    position: absolute;
    top: 0px;
  }
}

@media screen and (max-width: 700px) {
  .estiloLibro {
    width: 100%;
    height: 100%;
    border-radius: 0;
    background-color: #fbfbfb;
    padding: 0;
    margin-right: 0px;
  }

  .buscadores {
    justify-content: flex-start;
    align-items: start;
    flex-direction: column;
    height: 200px;
    padding-bottom: 20px;
    margin-bottom: 15px;
  }

  .search-libro {
    margin-left: 15px;
    width: 50%;
    height: 50%;
  }

  .search-genero {
    margin-left: 15px;
    width: 50%;
    height: 30%;
    margin-top: 2%;
    justify-content: flex-start;
  }

  .agregar-libro {
    width: 50%;
  }

  .libro-card button {
    background-color: white;
    border: none;
  }

  #boton-prestar {
    background-color: #ab4493;
    background-color: var(--darkpink);
  }

  .libro-card td > button ion-icon {
    color: #ab4493;
    color: var(--darkpink);
    border: none;
    width: 100%;
  }

  .libro-card td > button ion-icon:hover {
    color: #f045c8;
    color: var(--lightpink);
  }

  .libro-card td > button:hover {
    background-color: transparent;
  }

  .estiloLibro span {
    color: white;
  }

  .libro-card td > button {
    width: 80%;
    padding: 0;
